
import { __ } from '@wordpress/i18n';

import {
  TextControl, 
  PanelBody, 
  PanelRow, 
  RadioControl,
  __experimentalUnitControl as UnitControl, 
} from '@wordpress/components';

import {
  PanelColorSettings
} from '@wordpress/block-editor';


const helpLink = createElement(
  'a',
  { href: 'https://fontawesome.com/search', target: '_blank' },
  'FontAwesome'
);

const helpText = createElement(
  'span',
  {},
  helpLink,
  ' Unicode('
);

const units = [
  { value: 'px', label: 'px' },
  { value: 'em', label: 'em' },
  { value: 'rem', label: 'rem' },
];

export default ({ iconStyle, setAttributes })=>{
  const {
    icon_name,
    icon_pos,
    icon_size,
    icon_color,
    icon_space
  } = iconStyle;

  return(
    <>
      <TextControl
        label=""
        help={helpText}
        labelPosition="top"
        value={icon_name}
        isPressEnterToChange
        onChange={(newValue) =>{
          const newStyle = { ...iconStyle, icon_name: newValue};
          setAttributes({icon_style: newStyle});
        } }
      />

      <PanelRow className='sizing_row'>
        <UnitControl
          dragDirection="e"
          onChange={(newValue) =>{
            const newStyle = { ...iconStyle, icon_size: newValue};
            setAttributes({icon_style: newStyle});
          }} 
          label='' 
          value={ icon_size }
          units={units} 
        />
        <UnitControl
          dragDirection="e"
          onChange={(newValue) =>{
            const newStyle = { ...iconStyle, icon_space: newValue};
            setAttributes({icon_style: newStyle});
          }} 
          label='' 
          value={ icon_space }
          units={units} 
        />
      </PanelRow>
      
      <PanelColorSettings 
        title={ __( 'Color settings', 'itmar_location' ) }
        initialOpen={ false }
        colorSettings={ [
          {
            value: icon_color,
            onChange: (newValue) =>{
              const newStyle = { ...iconStyle, icon_color: newValue};
              setAttributes({icon_style: newStyle});
            },
            label: __( 'Icon color', 'itmar_location' )
          },
          
        ] }
      />
      <label className="components-base-control__label"></label>
      <PanelRow className='itmar_position_row'>
        <RadioControl
          selected={ icon_pos }
          options={ [
            { label: '', value: "left" },
            { label: '', value: "right" },
          ] }
          onChange={(newValue) =>{
            const newStyle = { ...iconStyle, icon_pos: newValue};
            setAttributes({icon_style: newStyle});
          }}
        />
      </PanelRow>
    </>
    
  );
}
