
import { __ } from '@wordpress/i18n';
import TypographyControls from '../TypographyControls'
import IconSelectControl from '../IconSelectControl';
import { ServerStyleSheet } from 'styled-components';
import { renderToString } from 'react-dom/server';
import { StyleComp } from './StyleWapper';

import {
	Button,
	Panel,
	PanelBody,
	PanelRow,
	ToggleControl,
	RangeControl,
	RadioControl,
	TextControl,
	__experimentalBoxControl as BoxControl,
	__experimentalUnitControl as UnitControl,
	__experimentalBorderBoxControl as BorderBoxControl
} from '@wordpress/components';
import {
	useBlockProps,
	RichText,
	BlockAlignmentControl,
	BlockControls,
	InnerBlocks,
	InspectorControls,
	PanelColorSettings,
	__experimentalPanelColorGradientSettings as PanelColorGradientSettings,
	__experimentalBorderRadiusControl as BorderRadiusControl
} from '@wordpress/block-editor';

import './editor.scss';
import { useDeepCompareEffect } from '../CustomFooks';

export default function Edit({ attributes, setAttributes }) {
	const {
		headingContent,
		font_style_heading,
		margin_heading,
		padding_heading,
		align,
		backgroundColor,
		backgroundGradient,
		textColor,
		radius_heading,
		border_heading,
		barWidth,
		colorVal_border,
		barSpace,
		gradientVal_border,
		color_text_copy,
		color_background_copy,
		gradient_background_copy,
		copy_content,
		font_style_copy,
		radius_copy,
		padding_copy,
		isIcon,
		icon_style,
		className,
	} = attributes;

	//
	const padding_resetValues = {
		top: '10px',
		left: '10px',
		right: '10px',
		bottom: '10px',
	}

	//
	const border_resetValues = {
		top: '0px',
		left: '0px',
		right: '0px',
		bottom: '0px',
	}

	const units = [
		{ value: 'px', label: 'px' },
		{ value: 'em', label: 'em' },
		{ value: 'rem', label: 'rem' },
	];
	//iframe
	useDeepCompareEffect(() => {
		const iframeInstance = document.getElementsByName('editor-canvas')[0];

		if (iframeInstance) {
			const iframeDocument = iframeInstance.contentDocument || iframeInstance.contentWindow.document;
			const sheet = new ServerStyleSheet();
			renderToString(sheet.collectStyles(<StyleComp attributes={attributes} />));
			const styleTags = sheet.getStyleTags();
			const styleContent = styleTags.replace(/<style[^>]*>|<\/style>/g, '');

			const iframeStyleTag = iframeDocument.createElement('style');
			iframeStyleTag.innerHTML = styleContent;

			// Append the new style tag to the iframe's document head
			iframeDocument.head.appendChild(iframeStyleTag);
		}
	}, [attributes]);



	return (
		<>
			<InspectorControls group="styles">
				<PanelBody title="" initialOpen={false} className="title_design_ctrl">
					<TypographyControls title='' fontStyle={font_style_heading} fontStyleName='font_style_heading' setAttributes={setAttributes} initialOpen={false} />

					<PanelColorGradientSettings
						title={__("Heading Color Setting")}
						settings={[{
							colorValue: textColor,
							label: __("Choose Text color"),
							onColorChange: (newValue) => setAttributes({ textColor: newValue }),
						},
						{
							colorValue: backgroundColor,
							gradientValue: backgroundGradient,

							label: __("Choose Background color"),
							onColorChange: (newValue) => setAttributes({ backgroundColor: newValue }),
							onGradientChange: (newValue) => setAttributes({ backgroundGradient: newValue }),
						},
						]}
					/>
					<BoxControl
						label=""
						values={margin_heading}
						onChange={value => setAttributes({ margin_heading: value })}
						units={units}	// 
						allowReset={true}	// 
						resetValues={padding_resetValues}	// 

					/>

					<BoxControl
						label=""
						values={padding_heading}
						onChange={value => setAttributes({ padding_heading: value })}
						units={units}	// 
						allowReset={true}	// 
						resetValues={padding_resetValues}	// 

					/>
					<PanelBody title="" initialOpen={false} className="border_design_ctrl">
						<BorderBoxControl
							colors={[{ color: '#72aee6' }, { color: '#000' }, { color: '#fff' }]}
							onChange={(newValue) => setAttributes({ border_heading: newValue })}
							value={border_heading}
							allowReset={true}	// 
							resetValues={border_resetValues}	// 
						/>
						<BorderRadiusControl
							values={radius_heading}
							onChange={(newBrVal) =>
								setAttributes({ radius_heading: typeof newBrVal === 'string' ? { "value": newBrVal } : newBrVal })}
						/>
					</PanelBody>
				</PanelBody>
				<PanelBody title="" initialOpen={false} className="title_design_ctrl">

					{className === 'is-style-virtical_line' &&
						<>
							<PanelColorGradientSettings
								title={__("Bar Color Setting")}
								settings={[{
									colorValue: colorVal_border,
									gradientValue: gradientVal_border,

									label: __("Choose Line Background"),
									onColorChange: (newValue) => setAttributes({ colorVal_border: newValue }),
									onGradientChange: (newValue) => setAttributes({ gradientVal_border: newValue }),
								},
								]}
							/>

							<PanelRow
								className='sizing_row'
							>
								<UnitControl
									dragDirection="e"
									onChange={(newValue) => setAttributes({ barWidth: newValue })}
									label=''
									value={barWidth}
								/>
								<UnitControl
									dragDirection="e"
									onChange={(newValue) => setAttributes({ barSpace: newValue })}
									label=''
									value={barSpace}
								/>
							</PanelRow>
						</>
					}

					{className === 'is-style-sub_copy' &&
						<>
							<PanelColorGradientSettings
								title={__("Copy Color Setting")}
								settings={[{
									colorValue: color_text_copy,
									label: __("Choose Text color"),
									onColorChange: (newValue) => setAttributes({ color_text_copy: newValue }),
								},
								{
									colorValue: color_background_copy,
									gradientValue: gradient_background_copy,

									label: __("Choose Background color"),
									onColorChange: (newValue) => setAttributes({ color_background_copy: newValue }),
									onGradientChange: (newValue) => setAttributes({ gradient_background_copy: newValue }),
								},
								]}
							/>

							<PanelRow
								className='copyInfo_row'
							>
								<TextControl
									label=""
									labelPosition="top"
									value={copy_content}
									isPressEnterToChange
									onChange={(newValue) => setAttributes({ copy_content: newValue })}
								/>
							</PanelRow>

							<TypographyControls title='' fontStyle={font_style_copy} fontStyleName='font_style_copy' setAttributes={setAttributes} />

							<PanelBody title="" initialOpen={true}>
								<BorderRadiusControl
									values={radius_copy}
									onChange={(newBrVal) =>
										setAttributes({ radius_copy: typeof newBrVal === 'string' ? { "value": newBrVal } : newBrVal })}
								/>

								<BoxControl
									label=""
									values={padding_copy}
									onChange={value => setAttributes({ padding_copy: value })}
									units={units}	// 
									allowReset={true}	// 
									resetValues={padding_resetValues}	// 

								/>
							</PanelBody>
							<PanelBody title="" initialOpen={true}>
								<ToggleControl
									label=''
									checked={isIcon}
									onChange={(newValue) => setAttributes({ isIcon: newValue })}
								/>
								{isIcon &&
									<IconSelectControl iconStyle={icon_style} setAttributes={setAttributes} />
								}
							</PanelBody>
						</>
					}
				</PanelBody>
			</InspectorControls>

			<div {...useBlockProps()}>
				<StyleComp attributes={attributes}>
					<RichText
						onChange={
							(newContent) => {
								setAttributes({ headingContent: newContent })
							}
						}
						value={headingContent}
						placeholder={__('Write your text...')}
					/>
				</StyleComp>
			</div>

		</>
	);
}
